.nav{
  grid-area: n;
  background-color: antiquewhite;
  padding: 20px 10px;
  border-radius: 10px;
}

.nav ul{
  list-style: none;
  margin: 0;
  padding: 0;
  margin-bottom: 50px;
}

.nav ul li:nth-child(4){
  margin-bottom: 20px;
}

.nav ul li:nth-child(5){
  margin-bottom: 20px;
}

li a{
  color: #b42d2d;
}

li a.item{
  color:  #e2cfcf;
}